.post {
    display: flex;
    flex-wrap: wrap;
    text-align: center;

    li {
        border: 2px solid black;
        margin: 2px;
        width: 45%;
        list-style: none;

        p {
            color: rgb(247, 4, 4);
        }
    }
}

@media screen and (max-width: 990px) {
    .post {
        display: flex;
        flex-direction: column;

        li {
            border: 2px solid black;
            margin: 2px;
            width: 100%;
            list-style: none;

            p {
                color: rgb(247, 4, 4);
            }
        }
    }

}