.home {
    min-height: calc(100vh - 54px);
    display: flex;
    flex-direction: column;
    justify-content: center;

    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        flex-wrap: wrap;
    }

    &__left,
    &__right {
        width: 45%;
    }

    &__left-descr {
        color: #0d0538;
        font-weight: 600;
    }

    &__left-position {
        text-transform: uppercase;
        color: rgb(235, 17, 17);
        font-weight: 600;
    }

    &__title {
        margin: 0;
        font-size: 2.4em;
        font-weight: 600;
        text-transform: uppercase;
        letter-spacing: 0.1em;
    }

    &__subtitle {
        margin: 0 0 15px;
        font-size: 1.4em;
        font-weight: 300;
    }

    &__links {
        margin: 0 0 25px;
        padding: 0;
        display: flex;
        list-style: none;
        gap: 20px;
    }

    &__icon {
        font-size: 2em;
        fill: lighten($color: #000000, $amount: 30);
        transition: 0.3s ease;

        &:hover {
            transform: scale(1.2);
            fill: #000000;
        }
    }

    &__cta {
        display: inline-block;
        background-color: #000000;
        padding: 10px 35px;
        border-radius: 6px;
        transition: 0.3s ease;
        color: #fff;

        &:hover {
            background-color: rgb(240, 218, 27);
            color: #0d0538;
        }
    }

    img {
        width: 100%;
    }
}

@media screen and (max-width: 768px) {
    .home {
        padding: 5vh 0;

        &__left,
        &__right {
            width: 100%;
            text-align: center;
        }

        &__right {
            order: -1;
        }

        &__links {
            justify-content: center;
        }
    }

}