.about {
    text-align: center;

    &__intro {
        margin-bottom: 45px;
        padding-top: 45px;

        h1,
        p {
            margin: 0;
        }
    }

    &__skills {
        margin-bottom: 45px;

        ul {
            margin: 0;
            padding: 0;
            list-style: none;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            gap: 20px;
        }

        li {
            background-color: #000;
            color: #fff;
            padding: 10px 35px;
            border-radius: 6px;
            display: flex;
            align-items: center;
            gap: 5px;

            svg {
                font-size: 1.5em;
            }
        }
    }

    &__portfolio {
        ul {
            margin: 0;
            padding: 0;
            list-style: none;
            display: flex;
            flex-wrap: wrap;
            gap: 30px;
        }

        li {
            position: relative;
            width: 30%;

            h4 {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                padding: 20px 10px;
                background-color: transparentize($color: #000000, $amount: 0.2);
                color: #fff;
                width: 100%;
                opacity: 0;
                transition: all 0.3s ease;
            }

            &:hover h4 {
                opacity: 1;
            }

            img {
                width: 100%;
                height: 300px;
                border-radius: 10px;
                object-fit: cover;
            }
        }
    }

    &__education {
        text-align: center;
        align-items: center;
        padding-left: 50px;

        h3 {
            margin-bottom: 10px;
            font-size: 25px;
            font-weight: 600;
        }

        ul {
            margin: 0;
            padding: 0;
            list-style: none;
            display: flex;
            flex-wrap: wrap;
            gap: 30px;
        }

        li {
            position: relative;
            width: 45%;

            h4 {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                padding: 20px 10px;
                background-color: transparentize($color: #000000, $amount: 0.2);
                color: #fff;
                width: 100%;
                opacity: 0;
                transition: all 0.3s ease;
            }

            &:hover h4 {
                opacity: 1;
            }

            img {
                width: 100%;
                height: 300px;
                border-radius: 10px;
                object-fit: cover;
            }

            p {
                margin-top: -40px;
                font-size: 18px;
                font-weight: 600;
                border: 1px solid black;
            }
        }
    }
}

